




















































































































































































































#tablets{
  position: relative;
  height: 100%;
  .queryBox {
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    text-align: right;
    .title {
      float: left;
      font-size: 14px;
      i {
        vertical-align: middle;
        font-size: 20px;
        margin-right: 5px;
      }
    }
  }
  .content_box {
    height: calc(100% - 80px);
    margin: 0 15px 0;
    .demo-split {
      max-height: 100%;
      overflow: auto;
      border: 1px solid #dcdee2;
      .demo-split-pane{
        padding: 10px;
        #fpbkList {
          width: calc(100% - 65px);
          margin: auto
        }
      }
      .right-pane {
        height: 100%;
        overflow: hidden;
        .demo-split-pane {
          height: 100%;
          overflow: hidden;
          background: #fff;
          .ivu-collapse-simple {
            height: calc(100% - 65px);
            overflow: auto;
            width: calc(100% - 65px);
            margin: auto;
            border: none !important;
            .ivu-collapse-header {
              overflow: hidden;
              .ivu-tag {
                float: right;
                margin-top: 9px;
              }
            }
            .ivu-collapse-content-box {
              #content {
                overflow: hidden;
                b {
                  float: left;
                  line-height: 28px;
                }
                ul {
                  float: left;
                  width: calc(100% - 160px);
                  margin-left: 15px;
                  li {
                    width: 100%;
                    overflow: hidden;
                    border-bottom: 1px dashed #f0f0f0;
                    line-height: 28px;
                    span {
                      float: left;
                      width: 70%;
                      overflow: hidden;
                      text-overflow: ellipsis;
                      white-space: nowrap;
                      text-align: left;
                    }
                    .deleteBtn {
                      float: right;
                      cursor: pointer;
                      color: #ed4014;
                      font-size: 20px;
                      vertical-align: middle;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .pBtn {
      margin-top: 15px;
    }
    .page_box{
      width: 100%;
      padding: 15px 0;
      text-align: right
    }
  }
  .btn-exportBtn {
    background: #2d579b;
    border-color: #2d579b;
    &:hover {
      background: #1a3a6f;
      border-color: #1a3a6f;
    }
  }
}
